.register{
  height: calc(100vh - 50px);
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  background: linear-gradient(rgba(255,255,255,0.5), rgba(255, 255, 255, 0.5)),
  url("C:\Users\pushp\Desktop\blog-react\public\register.avif");
  background-size: cover;
}

.registerTitle{
  font-size: 50px;
}

.registerForm{
  margin-top: 20px;
  display: flex;
  flex-direction: column;
}

.registerForm > label{
  margin: 10px 0;
}

.registerInput{
  padding: 10px;
  background-color: white;
  border: none;
}

.registerButton{
  margin-top: 20px;
  cursor: pointer;
  background-color: teal;
  border: none;
  color: white;
  border-radius: 10px;
  padding: 10px;
}

.registerLoginButton{
  position: absolute;
  top: 60px;
  right: 20px;
  background-color: lightcoral;
  cursor: pointer;
  color: white;
  border-radius: 10px;
  padding: 10px;
  border: none;
}