.login{
  height: calc(100vh - 50px);
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  background: linear-gradient(rgba(255,255,255,0.5), rgba(255, 255, 255, 0.5)),
  url("C:\Users\pushp\Desktop\blog-react\public\login.jpg");
  background-size: cover;
}

.loginTitle{
  font-size: 50px;
}

.loginForm{
  margin-top: 20px;
  display: flex;
  flex-direction: column;
}

.loginForm > label{
  margin: 10px 0;
}

.loginInput{
  padding: 10px;
  background-color: white;
  border: none;
}

.loginButton{
  margin-top: 20px;
  cursor: pointer;
  background-color: lightcoral;
  border: none;
  color: white;
  border-radius: 10px;
  padding: 10px;
}

.loginRegisterButton{
  position: absolute;
  top: 60px;
  right: 20px;
  background-color: teal;
  cursor: pointer;
  color: white;
  border-radius: 10px;
  padding: 10px;
  border: none;
}